import Header from "./form/header/Header";

import './App.css'
import Tags from "./form/Tags/Tags";
import About from "./form/About/About";
import { useEffect, useState } from "react";
import Loading from "./form/Loading/Loading";

function App() {

	const [ showAbout, setShowAbout] = useState(false);
	const [ isLoading, setLoading ] = useState(false);

	useEffect(() => {
		if (isLoading) {
			setTimeout( () => {
				setLoading(false)
			}, 1500)
		}
	}, [isLoading])

	if (isLoading) {
		return (
			<Loading />
		);
	}

	return (
		<div className="App">
			{
				!showAbout && (
					<>
						<Header />
						<Tags setShowAbout={setShowAbout} setLoading={setLoading} />
					</>
				)
			}
			{
				showAbout && <About showAbout={showAbout} setShowAbout={setShowAbout}/>
			}
		</div>
	);
}

export default App;
